@import "../../scss/constants/responsive.scss";

.courses {
    width: 100vw;
    padding-bottom: 100px;
    height: auto;
    min-height: 80vh;
    padding-top: 80px;
    background-color: white;
    //background-color: #ecedee;
    //background-color: #1C1D1F !important;
    border-width: 0px !important;

    .courses-header {
        margin-bottom: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        letter-spacing: 0.5px !important;
        font-size: 30px;
    }

    .top-container {
        margin-top: 20px;
        min-height: 100%;
        display: flex !important;
        justify-content: center;
        align-items: center;

        .courses-container {
            width: 100%;
            position: relative;

            &::-webkit-scrollbar {
                display: none;
            }

            .courses-card {
                padding: 0px !important;
                display: inline-block !important;
                width: 100%;
                margin: 20px 0px;
                max-width: 400px;
                justify-content: center;
                display: flex;
                box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                -webkit-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                -moz-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                white-space: normal;

                img {
                    min-width: 100%;
                    object-fit: contain;
                    height: auto;

                    @media screen and (min-width: 992px) {
                        //min-height: 200px;
                        //min-height: 224px;
                    }
                }

                .courses-body {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    padding: 15px !important;

                    h1 {
                        //font-family: inherit;
                        font-family: sans-serif;
                        font-weight: bold;
                        //font-size: 21px;
                        font-size: 19px;
                        padding-right: 20px;
                        display: -webkit-box;
                        height: 54px;
                        //letter-spacing: -0.25px;
                        letter-spacing: -0.5px;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        line-height: 27px;
                    }

                    .couse-author {
                        color: #6a6f73;
                        line-height: 13px;
                        font-size: 12px;
                        letter-spacing: -0.12px;
                        cursor: pointer !important;
                    }

                    p {
                        margin: 5px 0px 5px 0px;
                    }

                    .couse-information {
                        color: #6a6f73;
                        font-size: 12px;
                        letter-spacing: -0.12px;
                    }

                    .course-price {
                        color: black !important;
                        width: 150px;
                        font-weight: bolder;
                        font-size: 20px;
                        margin-top: 5px;
                        margin-bottom: -5px;
                        letter-spacing: -0.3px;
                        display: inline-block;
                        margin: auto;
                    }

                    .course-registered {
                        text-align: end;
                        width: calc(100% - 150px);
                        display: inline-block;
                        opacity: 0.9;
                        letter-spacing: -0.25px !important;
                        font-weight: bold;
                        color: black !important;
                        font-size: 18px;
                        margin-top: 5px;
                        margin-bottom: -5px;
                    }

                    .course-stars {
                        display: inline;
                        font-weight: bold;
                        color: #e7a22d !important;
                        display: flex;
                        align-items: center;

                        span.courseRating {
                            margin-top: 3.1px;
                            display: flex;
                            align-items: center;
                        }

                        span.stars {
                            margin-left: 5px;
                        }
                    }

                    .course-star {
                        margin-left: 1px;
                        font-size: 16px;
                        color: #e7a22d !important;
                    }
                }
            }
        }
    }
}